import React, { Component } from 'react';

class ErrorBoundryCardGrid extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hasError: false,
        }
    }

    componentDidCatch(error, info) {
        console.error(error, info);
        this.setState({ hasError: true });
    }

    render() {
        if(this.state.hasError) {
            return <h1>Error. No bots available.</h1>;
        }
        return this.props.children;
    }
}

export default ErrorBoundryCardGrid;